<template>
    <div
        :class="'widget' + (editing ? ' editing' : '') + (edit_inner ? ' edit_inner' : '') + ' ' + type"
        draggable="false"
        v-bind:style="{left: Math.min(x,a)+'%', top: Math.min(y,b)+'%', width: Math.abs(a-x)+'%', height: Math.abs(b-y)+'%'}"
        v-show=isShown
    >
        <div class="resize top"
             draggable="false"
            v-if="editing"
            v-bind:style="{top: 'calc('+y+'% - 6px)', left: 'calc(' + (x+a)/2 + '% - 5px)'}"
            v-on:mousedown="mouseDownResize"
        ></div>
        <div class="resize bottom"
             draggable="false"
            v-if="editing"
            v-bind:style="{top: 'calc('+b+'% - 4px)', left: 'calc('+(x+a)/2+'% - 5px)'}"
            v-on:mousedown="mouseDownResize"
        ></div>
        <div class="resize left"
             draggable="false"
            v-if="editing"
            v-bind:style="{left: 'calc('+x+'% - 4px)', top: 'calc('+(y+b)/2+'% - 5px)'}"
            v-on:mousedown="mouseDownResize"
        ></div>
        <div class="resize right"
             draggable="false"
            v-if="editing"
            v-bind:style="{left: 'calc('+a+'% - 4px)', top: 'calc('+(y+b)/2+'% - 5px)'}"
            v-on:mousedown="mouseDownResize"
        ></div>
        <div class="delete"
             draggable="false"
            v-if="editing"
            v-bind:style="{left: 'calc('+Math.max(x,a)+'% - 20px)', top: 'calc('+Math.min(y,b)+'% - 22px)'}"
            v-on:click="deleteWidget"
        ></div>
        <div class="move"
             draggable="false"
            v-if="editing"
            v-bind:style="{left: 'calc('+Math.max(x,a)+'% - 42px)', top: 'calc('+Math.min(y,b)+'% - 22px)'}"
            v-on:mousedown="moveWidget"
        ></div>
        <div class="edit"
             draggable="false"
            v-if="editing"
            v-bind:style="{left: 'calc('+Math.max(x,a)+'% - 64px)', top: 'calc('+Math.min(y,b)+'% - 22px)'}"
            v-on:click="editWidget"
        ></div>
        <div v-bind:class="'contents' + (this.editing ? ' editing' : '')">
            <iframe v-if="type == 'iframe'" v-bind:src="data['src']"></iframe>
            <div class="html" v-if="type=='html'" v-html="data.script"></div>
        </div>
        <HTMLModal
            v-if="type=='html' && editing"
            v-show="edit_inner"
            v-bind:html="data.script"
            v-on:save="editHTML"
            v-on:cancel="edit_inner=false"
        />
    </div>
</template>

<script>
import HTMLModal from './HTMLModal'

export default {
    name: 'widget',
    components: {
        HTMLModal,
    },
    props: ['x', 'y', 'a', 'b', 'editing', 'type', 'data'],
    data() {
        return {
            html_content: '',
            edit_inner: false,
            isShown: true,
        }
    },
    methods: {
        editHTML: function(newHTML) {
            this.$emit('changeHTML', newHTML)
            this.edit_inner = false
            this.refreshHTML()
        },
        refreshJS: function() {
            if (this.type != 'html') return
            if (!this.data.script) return
            var scripts = this.data.script.split('<script>')
            for (var i=1; i<scripts.length; i++) {
                eval(scripts[i].split('<\/script>')[0])
            }
        },
        mouseDownResize: function(e) {
            e.stopPropagation()
            switch(e.target.classList[1]) {
                case 'top':
                    this.$emit('resize', 1)
                    break;
                case 'bottom':
                    this.$emit('resize', 3)
                    break;
                case 'left':
                    this.$emit('resize', 0)
                    break;
                case 'right':
                    this.$emit('resize', 2)
                    break;
            }
        },
        deleteWidget: function(e) {
            e.stopPropagation()
            this.$emit('deleteWidget')
        },
        moveWidget: function(e) {
            this.$emit('moveWidget', e)
        },
        editWidget: function() {
            this.edit_inner = true
        },
        refreshHTML: function() {
            if (this.type == 'html') {
                this.refreshJS()
            }
        }
    },
    mounted() {
        this.isShown = false
        this.refreshHTML()
        this.isShown = true
    },
    watch: {
        editing: function() {
            this.refreshHTML()
        }
    },
}
</script>

