<template>
    <div id="app">
        <div id="canvas"
            v-on:mousedown="mouseDown"
            v-on:mousemove="mouseMove"
            v-on:mouseup="mouseUp"
        >
            <Widget
                v-for="(widget, index) in widgets"
                v-bind:key="index"
                v-bind:x=widget.box[0]
                v-bind:y=widget.box[1]
                v-bind:a=widget.box[2]
                v-bind:b=widget.box[3]
                v-bind:type=widget.type
                v-bind:data=widget.data
                v-bind:editing="editing"
                v-on:resize="(e) => resizeWidget(e,index)"
                v-on:deleteWidget="(e) => deleteWidget(e,index)"
                v-on:editWidget="(e) => editWidget(e, index)"
                v-on:changeHTML="(e) => changeHTML(e, index)"
                v-on:moveWidget="(e) => moveWidget(e, index)"
            />
            <Widget
                v-if="creating_corner_1[0] > -1"
                v-bind:x="Math.min(creating_corner_1[0], creating_corner_2[0])"
                v-bind:y="Math.min(creating_corner_1[1], creating_corner_2[1])"
                v-bind:a="Math.max(creating_corner_1[0], creating_corner_2[0])"
                v-bind:b="Math.max(creating_corner_1[1], creating_corner_2[1])"
                v-bind:editing="true"
            />
            <Toolbox
                v-show="editing"
                v-on:createWidget="createWidget"
                v-bind:selectedButton="creating"
            />
        </div>
        <label id="edit-switch">
            <input type="checkbox" v-model="editing">
            <span class="slider round"></span>
            <span class="text" v-if="editing">Editing</span>
            <span class="text" v-else>Viewing</span>
        </label>

        <ImportModal
                  v-if="editing"
                  v-show="import_open"
                  v-on:cancel="import_open=false"
                  v-on:import_text_event="import_text_event"
                  v-bind:import_text="JSON.stringify(widgets)"
          />
    </div>
</template>

<script>
import Toolbox from './components/Toolbox'
import Widget from './components/Widget'
import ImportModal from './components/ImportModal'
import Vue from 'vue'

export default {
    name: 'app',
    components: {
        Toolbox, Widget, ImportModal
    },
    data() {
        return {
            needConsent: this.$cookies.isKey('homepage-cookies') ? this.$cookies.get('homepage-cookies') : '',
            editing: false,

            creating: '',
            creating_corner_1: [-1,-1],
            creating_corner_2: [-1,-1],

            resizing: -1,
            resizing_side: -1,

            moving: -1,
            moving_diff: [-1,-1],

            widgets: this.$cookies.isKey('homepage-widgets') ? JSON.parse(this.$cookies.get('homepage-widgets')) : [],
            import_open: false,

            showHTMLModal: false,
        }
    },
    methods: {
        import_text_event: function(e) {
            this.widgets = JSON.parse(e)
        },
        changeHTML: function(newHTML, index) {
            this.widgets[index].data.script = newHTML
        },
        deleteWidget: function(e, index) {
            this.widgets.splice(index, 1)
        },
        editWidget: function(e, index) {
            this.widgets[index].data = this.createData(this.widgets[index].type)
        },
        moveWidget: function(e, index) {
            this.moving = index
            this.moving_diff = [
                e.clientX*100/window.innerWidth - (this.widgets[index].box[0]+this.widgets[index].box[2])/2,
                e.clientY*100/window.innerHeight - (this.widgets[index].box[1]+this.widgets[index].box[3])/2,
            ]
        },
        resizeWidget: function(e, index) {
            this.resizing = index
            this.resizing_side = e
        },
        createWidget: function(e) {
            if (e == 'import') {
                this.import_open = true;
            } else {
                this.creating = e
            }
        },
        mouseDown: function(e) {
            if (!this.editing) return
            if (!this.creating) return
            console.log(e);
            this.creating_corner_1 = [e.clientX*100/window.innerWidth, e.clientY*100/window.innerHeight]
            this.creating_corner_2 = [e.clientX*100/window.innerWidth, e.clientY*100/window.innerHeight]
        },
        mouseUp: function() {
            if(this.creating_corner_1[0] >= 0) {
                this.widgets.push({
                    box: [
                        Math.min(this.creating_corner_1[0], this.creating_corner_2[0]),
                        Math.min(this.creating_corner_1[1], this.creating_corner_2[1]),
                        Math.max(this.creating_corner_1[0], this.creating_corner_2[0]),
                        Math.max(this.creating_corner_1[1], this.creating_corner_2[1]),
                    ],
                    type: this.creating,
                    data: {},
                })

                this.creating = ''
                this.creating_corner_1 = [-1,-1]
                this.creating_corner_2 = [-1,-1]
            }
            if(this.resizing >= 0) {
                this.resizing = -1
            }
            if(this.moving >= 0) {
                this.moving = -1
            }
            this.save()
        },
        mouseMove: function(e) {
            if (this.creating_corner_1[0] >= 0) {
                Vue.set(this.creating_corner_2, 0, e.clientX*100/window.innerWidth)
                Vue.set(this.creating_corner_2, 1, e.clientY*100/window.innerHeight)
            }
            if (this.resizing >= 0) {
                Vue.set(this.widgets[this.resizing].box, this.resizing_side, [e.clientX*100/window.innerWidth, e.clientY*100/window.innerHeight, e.clientX*100/window.innerWidth, e.clientY*100/window.innerHeight][this.resizing_side])
            }
            if (this.moving >= 0) {
                var a = this.widgets[this.moving].box[0]
                var b = this.widgets[this.moving].box[1]
                var c = this.widgets[this.moving].box[2]
                var d = this.widgets[this.moving].box[3]
                Vue.set(this.widgets[this.moving].box, 0, e.clientX*100/window.innerWidth - this.moving_diff[0] - Math.abs((a-c)/2))
                Vue.set(this.widgets[this.moving].box, 1, e.clientY*100/window.innerHeight - this.moving_diff[1] - Math.abs((b-d)/2))
                Vue.set(this.widgets[this.moving].box, 2, e.clientX*100/window.innerWidth - this.moving_diff[0] + Math.abs((a-c)/2))
                Vue.set(this.widgets[this.moving].box, 3, e.clientY*100/window.innerHeight - this.moving_diff[1] + Math.abs((b-d)/2))
            }
        },
        save: function() {
            this.$cookies.set('homepage-widgets', JSON.stringify(this.widgets), -1)
        },
    },
}
</script>

<style lang="scss">
body {
    padding: 0;
    margin: 0;
    height: 100vh;
    width: 100vw;
}

#app {
    height: 100%;
    width: 100%;
}
#canvas {
    position: fixed;
    height: 100%;
    width: 100%;
}

 /* The switch - the box around the slider */
#edit-switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

    input {
        opacity: 0;
        width: 0;
        height: 0;
    }
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

#edit-switch .text {
  padding-top: 10px;
  display: inline-block;
}
</style>
