<template>
    <div id="toolbox" v-bind:style="{left: left+'px', top: top+'px'}">
        <div id="move-bar"
             v-bind:class="{moving: moving}"
             v-on:mousedown="mouseDownMove"
             v-on:mouseup="mouseUpMove"
             v-on:mousemove="mouseMoveMove"
             v-on:mouseleave="mouseUpMove"
        ></div>

        <div
             v-for="button in buttons"
             v-bind:key="button"
             v-bind:id="button"
             v-bind:style="{backgroundImage: 'url('+button+'.png)'}"
             v-bind:class="'widget-selector' + (selectedButton==button ? ' selected' : '')"
             v-on:click="emitCreate"
        >
        </div>
    </div>
</template>

<script>
export default {
    name: 'Toolbox',
    data() {
        return {
            moving: false,
            left: this.$cookies.isKey('toolbar-left') ? this.$cookies.get('toolbar-left') : 100,
            top: this.$cookies.isKey('toolbar-top') ? this.$cookies.get('toolbar-top') : 100,

            selectLeft: -1,
            selectTop: -1,

            buttons: ['box', 'text', 'html', 'import'],
        }
    },
    props: ['selectedButton'],
    methods: {
        emitCreate: function(e) {
            if (e.target.id == this.selectedButton) {
                this.$emit('createWidget', '')
            } else {
                this.$emit('createWidget', e.target.id)
            }
        },
        mouseDownMove: function(e) {
            this.moving = true
            this.selectLeft = e.layerX
            this.selectTop = e.layerY
        },
        mouseUpMove: function() {
            this.moving = false
        },
        mouseMoveMove: function(e) {
            if(this.moving) {
                this.left = e.pageX - this.selectLeft
                this.top = e.pageY - this.selectTop

                this.$cookies.set('toolbar-left', this.left)
                this.$cookies.set('toolbar-top', this.top)
            }
        }
    }
}
</script>

<style lang="scss">
#toolbox {
    border: 2px solid black;
    display: inline-block;
    position: fixed;
    width: 208px;
    background-color: white;
    z-index: 100;

    #move-bar {
        cursor: grab;
        width: 100%;
        height: 30px;
        background-color: grey;

        &.moving {
            cursor: grabbing;
        }
    }

    .widget-selector {
        width: 40px;
        height: 40px;
        margin: 5px;
        border: 1px solid black;
        display: inline-block;
        cursor: pointer;
        position: relative;
        background-size: cover;

        &.selected {
            background-color: blue;
        }
    }
}
</style>
