<template>
    <div class="import-modal">
        <textarea v-model="import_text" ></textarea>
        <div class="buttons">
            <button v-on:click="cancel">Cancel</button>
            <button v-on:click="import_text_event">Import</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HTMLModal',
    props: ['import_text'],
    data() {
        return {
        }
    },
    methods: {
        import_text_event: function() {
            this.$emit('import_text_event', this.import_text)
        },
        cancel: function() {
            this.$emit('cancel')
        },
    },
}
</script>

