<template>
    <div class="edit-modal">
Paste the html code to be displayed here.  This can include javascript, but keep it between simple script tags.
        <textarea v-model="html" ></textarea>
        <div class="buttons">
            <button v-on:click="cancel">Cancel</button>
            <button v-on:click="save">Save</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'HTMLModal',
    props: ['html'],
    data() {
        return {
        }
    },
    methods: {
        save: function() {
            this.$emit('save', this.html)
        },
        cancel: function() {
            this.$emit('cancel')
        },
    },
}
</script>

